import React from 'react';
import CoupleCard from './CoupleCard';
import styled from 'styled-components';
import { Spinner, Container, Row, Col } from 'react-bootstrap' 
import ContentLoader from "react-content-loader"
import { skeletonClasses } from '@mui/material';
import ScrollToBottom from 'react-scroll-to-bottom';


const StyledContainer = styled(Container)`
  display: block;
  text-align: center;
  margin-top: 10px;
`;

const ContentLoaderStyled = styled(ContentLoader)`
  background-color: white;
  border-radius: 2px;
  box-shadow: 2px 2px 2px rgb(175, 175, 175);
  margin-bottom: 30px;
`;

const StyledRow = styled(Row)`
  max-width: 350px;
  margin: auto;
  transition: all 1s ease-out;
`;

function ListWithRendered({data, arrayRendered}) {
  let [loading, setLoading] = React.useState(true);
  let [isSkeleton, setisSkeleton] = React.useState(true);
  let [couplesArr, setCouplesArr] = React.useState([]);

  console.log("arrayRendered", arrayRendered)

  // let delay = Math.floor(data.process.duration / data.gangs.length * 1000);
  let delay = 3000;

  const messagesEndRef = React.useRef(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  React.useEffect(() => {
    setCouplesArr(arrayRendered);
   
      for (let i = 0; i < (data.gangs.length - arrayRendered.length); i++) {
        setTimeout(() => {
          setCouplesArr((prevState) => [...prevState, data.gangs[arrayRendered.length + i]])
          scrollToBottom()
        }
        , delay * i);
      }
    
      setTimeout(() => {
        setisSkeleton(false)
        
      }
      , delay * (data.gangs.length - arrayRendered.length - 1));
    
  }, []);


  return (
    <StyledContainer>
      <StyledRow>
      {loading ? (couplesArr && couplesArr.map((item) => {
        return (
        <Col md={6} lg={4} ref={messagesEndRef}>
          <CoupleCard data={item} />
      </Col>)}
        
      )) : <Spinner animation="grow" variant="danger" />}
      {isSkeleton && <ContentLoaderStyled 
        speed={2}
        width={350}
        height={46}
        viewBox="0 0 350 46"
        backgroundColor="#c2c2c2"
        foregroundColor="#ffffff"
      >
      <rect x="50" y="4" rx="5" ry="5" width="250" height="14" /> 
      <rect x="0" y="22" rx="5" ry="5" width="350" height="1" /> 
      <rect x="50" y="28" rx="5" ry="5" width="250" height="14" />
    </ContentLoaderStyled>}
      </StyledRow>
    </StyledContainer>
  );
}

export default ListWithRendered;
