import List from './List'
import React from 'react'
import axios from 'axios'
import { CircularProgress } from '@mui/material'

let URL = 'https://sheltered-mountain-91837.herokuapp.com/api/v1/generations/1';
let URL2 = 'https://61e572d1c14c7a0017124c77.mockapi.io/123'
let URL3 = 'https://sheltered-mountain-91837.herokuapp.com/api/v1/generations/latest'


const App = () => {
  let [data, setData] = React.useState({})
  let [pairs, setPairs] = React.useState({})
  React.useEffect(() => {
    axios.get(URL2).then((res) => {
      setData(res.data[0]);
      setPairs(res.data[0].gangs);
      
    })
  }, [])

  return (
    <div>
        {(pairs.length > 0) ? <List data={data} /> : <CircularProgress color='secondary' />}
    </div>
  )
}


{/* <List data={data} /> */}
export default App;