import React from 'react';
import ListWithRendered from './ListWithRendered';

const List = ({data}) => {

  let [renderedPairsArray, setRenderedPairsArray] = React.useState([]);

  React.useEffect(() => {
      const timeOfRequest = data.timeOfRequest;
      const createdAt = data.process.createdAt;

      console.log("data.gangs", data.gangs)
      
      const diff = Math.floor((Date.parse(timeOfRequest)-Date.parse(createdAt))/1000)
      let renderedPairs = Math.floor(diff / (data.process.duration / data.gangs.length));

      console.log("data.gangs.length", data.gangs.length)

      if (renderedPairs > data.gangs.length) { 
        renderedPairs = data.gangs.length
      }
      for (let i = 0; i < renderedPairs; i++) {
        setRenderedPairsArray((prevState) => [...prevState, data.gangs[i]])
        
      }
  }, [])

    console.log("renderedPairs", renderedPairsArray)

  return (
<>
    {renderedPairsArray.length > 0 && <ListWithRendered data={data} arrayRendered={renderedPairsArray}/>}
</>
  )
}

export default List;