import React from 'react'
import './App.css'

const CoupleCard = ({data}) => {
  let gender = data.gangsters[0].gender;
  return (
    <div className="card">
          {(data.gangsters.length === 2) ? (
            <div className={gender === "FEMALE" ? "female": "male"}>
            <span>{data.gangsters[0].name}</span>
            <hr />
            <span>{data.gangsters[1].name}</span>
            </div>
          ) : (
            <div className={gender === "FEMALE" ? "female": "male"}>
            <span>{data.gangsters[0].name}</span>
          <hr />
          <span>{data.gangsters[1].name}</span>
          <hr />
          <span>{data.gangsters[2].name}</span>
          </div>
          )}
          
    </div>
  )
}

export default CoupleCard;